	module.exports = {"data":{"wordpressPost":{"title":"Top 10 Most Read in July: Link Worker social prescribing, best practice in sex and relationship education, and menstrual hygiene management among Bangladeshi adolescent schoolgirls","content":"<p><img class=\"alignnone\" title=\"Image: Blandade Kondomer, by Christian George, Public Domain Pictures\" src=\"http://www.publicdomainpictures.net/pictures/210000/velka/assorted-condoms.jpg\" alt=\"Image result for colourful condoms\" width=\"328\" height=\"246\" /></p>\n<p>An assortment of study designs made it into the Top 10 Most Read articles in July: among the popular studies were a systematic review, a protocol for a prospective observational study, and a qualitative study of service user perceptions.</p>\n<p>At number one this month is a longitudinal cohort study by Xie <em>et al</em> examining the association between Proton Pump Inhibitors and the risk of all-cause mortality. Moffatt <em>et al</em> enter the chart at number two with their study on Link Worker social prescribing to improve health and well-being for people with long-term conditions. Undertaking a qualitative study using semistructured interviews with thematic analysis, they seek to describe the experiences of patients with long-term conditions who are referred to and engage with a Link Worker social prescribing programme and identify the impact of this programme.</p>\n<p>Reaching number three this month is a study from Pound <em>et al</em> looking at what makes sex and education programmes effective, acceptable and sustainable. At number four is a systematic review investigating how different terminology used for the same condition can influence management preferences and psychological outcomes, concluding that changing the terminology used may be one strategy to reduce patient preferences for aggressive management responses to low-risk conditions. A cross-sectional study on the use of snus and its association with respiratory and sleep-related symptoms remains in the top ten this month at number six, while at number ten we have a new entry from Bangladesh examining the association of menstrual hygiene management knowledge, facilities and practice with absence from school during menstruation among Bangladeshi schoolgirls.</p>\n<table border=\"1\" width=\"483\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Xie <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/6/e015735\">Risk of death among users of Proton Pump Inhibitors: a longitudinal observational cohort study of United States veterans</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Moffatt<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/7/e015203\">Link Worker social prescribing to improve health and well-being for people with long-term conditions: qualitative study of service user perceptions</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Pound<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\">\n<div class=\"highwire-cite-title\"><a href=\"http://bmjopen.bmj.com/content/7/5/e014791\">What is best practice in sex and relationship education? A synthesis of evidence, including stakeholders views</a></div>\n</td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Nickel <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/7/e014129\">Words do matter: a systematic review on how different terminology for the same condition influences management preferences</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Rambaud <em>et al</em><a href=\"http://bmjopen.bmj.com/content/7/6/e015087\">.</a></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/6/e015087\">Criteria for Return to Sport after Anterior Cruciate Ligament reconstruction with lower reinjury risk (CRSTAL study): protocol for a prospective observational study in France</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Gudnadóttir <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/5/e015486\">An investigation on the use of snus and its association with respiratory and sleep-related symptoms: a cross-sectional population study</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Ooba et<em> al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/6/e015935\">Lipid-lowering drugs and risk of new-onset diabetes: a cohort study using Japanese healthcare data linked to clinical data for health screening</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Kennedy<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/5/e013739\">Coffee, including caffeinated and decaffeinated coffee, and the risk of hepatocellular carcinoma: a systematic review and dose-response meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Fabbri<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/6/e016701\">A cross-sectional analysis of pharmaceutical industry-funded events for health professionals in Australia</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Alam <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/7/e015508\">Menstrual hygiene management among Bangladeshi adolescent schoolgirls and risk factors affecting school absence: results from a cross-sectional survey</a></td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p><em>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.</em><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"d121af45-3dd8-5b36-aa84-91e9e3c2259d"}}

